<template>
  <section class="bank-table">
    <div class="bank-table__filters">
      <RadioButtons v-model="depositType" :data="radioFilter">
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </RadioButtons>
      <div class="bank-table__filters__actions">
        <Button
          class="bank-table__filters__actions-button"
          type="primary"
        >
          Deposit LTC
        </Button>
        <Button
          class="bank-table__filters__actions-button"
          type="primary"
        >
          Withdraw LTC
        </Button>
      </div>
    </div>
    <Input
      placeholder="Search"
      class="bank-table__search"
    >
      <template #prepend>
        <Icon name="search" />
      </template>
    </Input>

    <Table
      :columns="tableCols"
      :data="tableData"
      class="bank-table__table"
      @copy-cell="copyCell"
    >
      <template #column="{ column }">
        {{ column.title }}
      </template>
      <template #cell="{ column, item }">
        <span
          v-if="item && column.name === 'value'"
          class="bank-table__table-value"
          :class="{ 'bank-table__table-value_down': !item.up }"
        >
          <Icon
            :name="`arrow-${item.up ? 'up' : 'down'}-full`"
            class="bank-table__table-value__icon"
          />
          {{ item.percents }}%
          <pre v-if="item.up"> | ${{ item.amount }}</pre>
          <pre v-else> (${{ item.amount }})</pre>
        </span>
        <Badge
          v-else-if="column.name === 'status'"
          :type="COLORS[item]"
        >
          {{ STATUS[item] }}
        </Badge>
        <component
          v-else
          :is="column.component || 'span'"
          :type="COLORS[item]"
        >
          {{ item }}
        </component>
      </template>
    </Table>
  </section>
</template>

<script>
import { ref } from 'vue'
import { radioFilter, tableCols, tableData } from '@/store/mock/bankMock'
import { STATUS, COLORS } from '@/common/status'
import { copy } from '@/utils/copyToClipboard'
import RadioButtons from '@/components/UIKit/RadioButtons'
import Button from '@/components/UIKit/Button'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import Table from '@/components/Table/Table'
import Badge from '@/components/UIKit/Badge'

export default {
  name: 'BankTable',
  components: { Badge, Table, Icon, Input, Button, RadioButtons },
  setup () {
    const depositType = ref(null)

    const copyCell = ({ col, item }) => {
      copy(item[col.name])
    }
    return {
      depositType,
      radioFilter,
      tableCols,
      tableData,
      STATUS,
      COLORS,
      copyCell
    }
  }
}
</script>

<style lang="scss">
.bank-table {
  margin-top: 20px;

  &__filters {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__actions {
      &-button {
        margin-left: 15px;
        padding: 10px 20px;

        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
  &__search {
    margin-top: 20px;
  }
  &__table {
    margin-top: 10px;

    &-value {
      display: flex;

      &_down {
        opacity: 0.5;
      }
      &__icon {
        margin-right: 7px;
      }
    }
  }
}
</style>
