
export const STATUS = Object.freeze({
  unconfirmed: 'Unconfirmed',
  new: 'New',
  expired: 'Expired',
  complete: 'Complete',
  confirmed: 'Confirmed',
  progress: 'In Progress'
})

export const COLORS = Object.freeze({
  unconfirmed: 'primary-light',
  new: 'info',
  expired: 'secondary-light',
  complete: 'success',
  confirmed: 'success',
  progress: 'success'
})

export const STATUS_SELECT = Object
  .keys(STATUS)
  .map(key => {
    return {
      id: key,
      key,
      title: STATUS[key],
      color: COLORS[key]
    }
  })
